export default {
	data() {
		return {
			isSelect: true,
			time: 600,
			timer: null,
			timeArr: [],
			order_price: null,
			order_id: '',
			noClick: false,
			showPayBtn: false,
			showBlance:false
		}
	},
	onLoad(options) {
		console.log(options)
		this.order_price = options.order_money
		this.order_id = options.order_id
		this.countDown()
	},
	onShow() {
		
	},
	onHide() {
		clearInterval(this.timer)
	},
	methods: {
		selectPayment() {
			this.isSelect = !this.isSelect
		},
		//去支付
		pay() {
			if(!this.isSelect){
				this.showToast('请选择支付方式')
				return
			}
			if(this.noClick){
				this.showToast('请勿重复点击')
				return
			}
			this.noClick = true
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/Order/pay',
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.order_id
				},
				header: this.header,
				method: "POST",
				success: (res) => {
					console.log(res.data)
					// this.noClick = false
					if (res.data.status == 0) {
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: res.data.data.timeStamp,
							nonceStr: res.data.data.nonceStr,
							package: res.data.data.package,
							signType: res.data.data.signType,
							paySign: res.data.data.paySign,
							success: (res) => {
								this.noClick = false
								console.log('success:' + JSON.stringify(res));
								uni.redirectTo({
									url: "../paySuccess/paySuccess?type=1&orderid=" + this.order_id
								})
							},
							fail: (err) => {
								this.noClick = false
								console.log('fail:' + JSON.stringify(err));
							}
						});
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//十分钟倒计时
		countDown() {
			var _this = this
			this.timer = setInterval(function() {
				_this.time--
				if (_this.time == 0) {
					clearInterval(_this.timer)
					// uni.showToast({
					// 	title: '订单支付超时',
					// 	icon: 'none',
					// 	success: () => {
					// 		setTimeout(function() {
					// 			uni.reLaunch({
					// 				url: '../index/index'
					// 			})
					// 		}, 1500);
					// 	}
					// })
				}
				_this.timeArr = [parseInt(_this.time / 60).toString().padStart(2, '0'), parseInt(_this.time % 60).toString().padStart(
					2, '0')]
			}, 1000)
		}
	}
}
